let Tool = (function () {
    let toolObj = {};
  
    function getCookie (cname) {
      var name = cname + "=";
      var ca = document.cookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') c = c.substring(1);
        if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
      }
      return "";
    }
    function setCookie(name,value,path,domain,exp) 
    { 
        var now = new Date(); 
        now.setTime(now.getTime() + exp*1000); 
        var expiryString = exp?"; expires=" + now.toGMTString():"";
        var pathString = (path == null) ? " ;path=/" : " ;path = " + path;
        var domainString = (domain == null) ? " ;domain = " + gloDomain : " ;domain = " + domain;
        document.cookie = name + "="+ escape (value) + expiryString+pathString+domainString; 
    } 

    function getCharLength(value,maxlength){
      
          // 取出回车字符
          //  var textareaVal = value.replace(/\n/gi,"|");
          // 回车数量
           var entLen = value.split('\n').length - 1;
          // 不包含回车的数量
           var strLen = value.replace(/\n/gi, '').length;
          // var len = 0;
          // if (navigator.userAgent.match(/(iPhone|iPod|Android|ios|SymbianOS|Windows Phone)/i)) {
              // len = entLen * 2 + strLen;
          // }
          // else {
             var len = entLen + strLen;
          // }
           return len>maxlength?maxlength:len;
          // return value.length;
          
    }

    function getTerminal () {
      var terminal = "msite";
      if (this.getCookie("cdcfromqq") == 1) {
        terminal = "QQ";
      }
      if (this.getCookie("fromApp") == 1) {
        terminal = "app";
      }
      return terminal;
    }
  
    toolObj.getCookie = getCookie;
    toolObj.setCookie=setCookie;
    toolObj.getTerminal = getTerminal;
    toolObj.getCharLength = getCharLength;
    return toolObj;
  })();
  export default Tool;
  


