    //   name: 'ExaminationSearch',
    //   component: ExaminationSearch
    // },
    // {
    //   meta: {
    //     title: '日常'
    //   },
    //   path: '/iv_schedule',
    //   name: 'Schedule',
    //   component: Schedule
    // },
    // {
    //   meta: {
    //     title: '简历详情页'
    //   },
    //   path: '/iv_resumedetails',
    //   name: 'ResumeDetails',
    //   component: ResumeDetails
    // },
    // {
    //   meta: {
